<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="warningHeader backgroundNaviBar">
      <div slot="left" color="#3051f1">
        <a @click="toBack()">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">预警管理</span>
      </div>
      <div slot="right" class="icon" size="20" @click="openBotttomSheet">
        <mu-button icon slot="left" class="icon" size="20">
          <img src="static/img/queryNews.png" alt srcset />
        </mu-button>
      </div>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      :title="$store.state.warningStation"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="()=>{
           open = true
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <van-icon name="filter-o" slot="right" size="20" />
    </van-nav-bar>
    <!-- <div class="statusTitle">
      <div class="flex">
        <div @click="handled()" :style="styleHandle">已处理</div>
        <div @click="nohandled()" :style="styleNoHandle">未处理</div>
      </div>
    </div>-->
    <div class="positionF" v-show="showSubmit===true">
      <div class="flexContentHeader" style="padding:0 10px; background:white;border-radius:10px;">
        <div style="flex:2;" class="flexContainerR">
          <span style="margin-right:10px;">预警条数</span>
          <span style="color:red;">{{total}} 条</span>
        </div>
        <div style="flex:2" v-show="showSubmit===false">
          <span>未处理条数：</span>
          <span style="color:red">{{undo}} 条</span>
        </div>
        <div class="btnHandleWarning" style="flex:2" v-show="showSubmit===true">
          <span class="piliang" @click="showCheckedFunc" style="margin-right:10px;">批量处理</span>
          <span class="rightSumbit backgroundNaviBar" @click="handledTo('批量处理')">提交</span>
        </div>
      </div>
    </div>
    <div class="warningContent">
      <!-- <mu-expansion-panel> -->

      <div style="padding:0 10px; background:white;margin:10px;height:100%; " class="border-radius">
        <!-- <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit"> -->

        <mu-container ref="container">
          <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
            <mu-expansion-panel
              v-for="(item,index) in listWarningContent"
              :key="index"
              :expand="panel === 'panel1'+index"
              @change="toggle('panel1'+index,index)"
              class="boxShow"
              style="padding:0 10px;"
            >
              <div slot="header" style="color:rgb(129, 119, 119)" class="headerListAlert">
                <div class="checkedTitle">
                  <!-- <span>{{(index+1)+" "}}</span> -->
                  <img
                    v-show="showChecked"
                    @click="checkedData(item,index,item.gmtModified)"
                    :src="item.img"
                    alt
                  />
                  <span>{{item.warningType}}</span>
                </div>
                <!-- <span style="color:green" v-show="item.showIndex">已选定</span> -->
                <div v-show="showSubmit===true">
                  <button
                    class="btnStyles"
                    v-show="item.warningStatus===0"
                    @click="backTonoHandleNo(item.warningId)"
                  >未处理</button>
                  <button
                    class="btnStylesHandled"
                    v-show="item.warningStatus===2"
                    @click="backTonoHandle(item.warningId,index)"
                  >已处理</button>
                  <button class="btnStylesHandlding" v-show="item.warningStatus===1">正在处理</button>
                </div>
                <!-- <van-checkbox @click="checkedData" :name="index" shape="square"></van-checkbox> -->
              </div>
              <div>
                <div class="bettweenFlexBack">
                  <div class="bettweenFlex">
                    <span>编号</span>
                    <span>{{item.warningId}}</span>
                  </div>
                  <div class="bettweenFlex">
                    <span>预警时间</span>
                    <span>{{item.gmtModified}}</span>
                  </div>
                  <div class="bettweenFlex">
                    <span>预警站点</span>
                    <span>{{item.warningStation}}</span>
                  </div>
                  <!-- <div class="bettweenFlex">
                    <span>处理人</span>
                    <span>{{item.processingPerson}}</span>
                  </div>-->
                  <div class="bettweenFlex">
                    <span>预警类型</span>
                    <span>{{item.warningType}}</span>
                  </div>
                  <div class="bettweenFlex" v-show="showSubmit===true">
                    <div>处理状态</div>
                    <div>
                      <span class="btnStylesCont" v-if="item.warningStatus===0">未处理</span>
                      <span class="btnStylesHandledCont" v-if="item.warningStatus===1">已处理</span>
                    </div>
                  </div>
                  <div>
                    <span>预警内容</span>
                  </div>
                  <div class="paddingContent">
                    <div class="panelContent" v-for="(itemChild,index1) in content" :key="index1">
                      <div class="bettweenFlex">
                        <span>{{itemChild[0].name}}</span>
                        <span style="color:red">{{itemChild[0].value}}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </mu-expansion-panel>
          </mu-load-more>
        </mu-container>
        <mu-container>
          <mu-bottom-sheet :open.sync="open" class="queryPadding">
            <mu-list @item-click="closeBottomSheet">
              <mu-list-item>
                <div class="titleselect">
                  <span>站点名称：</span>
                </div>
                <div class="select" @click="selectDataPicoptions" id="classPicoptions">
                  <input
                    type="text"
                    placeholder="请选择站点"
                    class="input"
                    disabled
                    v-model="normal.value1"
                  />
                  <img src="static/img/toBottom.png" alt srcset />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="titleselect">
                  <span>预警类型：</span>
                </div>
                <div class="select" @click="selectDataclassType" id="classType">
                  <input
                    type="text"
                    placeholder="请选择预警类型"
                    class="input"
                    disabled
                    v-model="warningType"
                  />
                  <img src="static/img/toBottom.png" alt srcset />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="titleselect">
                  <span>开始时间：</span>
                </div>
                <div class="select" @click="selectStartTime('start')">
                  <input type="text" class="input" disabled v-model="value1" />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="titleselect">
                  <span>结束时间：</span>
                </div>
                <div class="select" @click="selectLastTime('end')">
                  <input type="text" class="input" disabled v-model="value2" />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="btnBottom">
                  <div class="left" @click="closeBottomSheet()">取消</div>
                  <div
                    class="right backgroundNaviBar"
                    :style="'background:'+$store.state.color"
                    @click="initData()"
                  >确定</div>
                </div>
              </mu-list-item>
            </mu-list>
          </mu-bottom-sheet>
        </mu-container>
        <mu-container>
          <mu-bottom-sheet :open.sync="openTime">
            <mu-list @item-click="closeBottomSheetTime">
              <van-datetime-picker
                v-model="currentDate"
                type="date"
                :min-date="minDate"
                @confirm="confirmTime"
                @cancel="close"
              />
            </mu-list>
          </mu-bottom-sheet>
        </mu-container>
      </div>
    </div>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
// import Vue from 'vue';
import { Checkbox, CheckboxGroup, Overlay, Loading } from "vant";
Vue.use(Checkbox)
  .use(CheckboxGroup)
  .use(Overlay)
  .use(Loading);
import request from "../../../static/js/request.js";
import Warning from "../../../static/js/warning.js";
import WarningHandled from "../../../static/warning/index.js";
// import MescrollVue from "mescroll.js/mescroll.vue";
import AppBace from "../../../static/ajax.js";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from "vant";
const h = document.documentElement.clientHeight || document.body.clientHeight;
const heightDevice = Math.floor((h - 90 - 56) / 58);
export default {
  name: "warningControl",
  data() {
    return {
      showChecked: false,
      checked: "",
      refreshing: false,
      loading: false,
      listWarningContent: [],
      token: "",
      panel: "",
      content: [],
      openSimple: false,
      open: false,
      value1: AppBace.DateTime(7),
      value2: AppBace.filter(new Date()),
      options: [],
      normal: { value1: "" },
      styleHandle:
        "border-bottom-color:" +
        this.$store.state.color +
        ";color:" +
        this.$store.state.color,
      showHandled: false,
      showNoHandled: false,
      styleNoHandle:
        "border-bottom-color:" +
        this.$store.state.color +
        ";color:" +
        this.$store.state.color,
      list: [],
      openTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      Time: "",
      currentPage: 1,
      sizeContent: 0,
      pageSizeCont: heightDevice,
      sizeContentAll: 100,
      warningType: "因子超标预警",
      warningTypeArr: [
        "因子超标预警",
        "设备异常预警",
        "数据因子缺失",
        "数据丢失"
      ],
      arrIndexList: [],
      timePermision: 0,
      startTimeSelect: 0,
      times: 0,
      total: 0,
      showSubmit: true,
      showover: false,
      undo:0
    };
  },
  created() {
    this.user = sessionStorage.getItem("user");
    if (this.user === "manager") {
      this.showSubmit = false;
      this.warningTypeArr = ["因子超标预警"];
    }
    this.showover = true;
  },
  methods: {
    showCheckedFunc() {
      this.showChecked = !this.showChecked;
      this.clearData();
    },
    checkedData(event, index, time) {
      var arr = this.arrIndexList;
      // var startTimeSelect = 0;
      var endTime = 0;
      // var timePermision = 0;
      // this.times = 0;
      if (this.arrIndexList.length === 0) {
        // this.times = 0;
        this.startTimeSelect = 0;
        this.startTimeSelect = Number(new Date(time));
        // this.times++;
        this.timePermision = Number(new Date(time)) - 6 * 60 * 60 * 1000;
        var array = this.listWarningContent;

        for (let index = 0; index < array.length; index++) {
          var color = array[index].color;
          var time12 = array[index].gmtModified;
          var endTimes = Number(new Date(time12));
          if (
            endTimes >= this.timePermision &&
            endTimes <= this.startTimeSelect
          ) {
            array[index].color = "background:white";
          } else {
            array[index].color = "background:rgb(233, 233, 233)";
            // 不可选
            array[index].img = "static/imgs/noChecked.png";
          }
        }
        this.listWarningContent = array;
        this.warningDataSelect(index, event);
      } else if (this.arrIndexList.length >= 1) {
        endTime = Number(new Date(time));
        if (endTime >= this.timePermision && endTime <= this.startTimeSelect) {
          this.warningDataSelect(index, event);
        } else {
          var _this = this;
          Toast("批量处理只可以连续处理6个小时内！");
        }
      } else {
      }
    },
    handleWarning() {
      var e = "";
      var status = false;
      if (this.user !== "manager") {
        if (this.arrIndexList.length > 1) {
          this.$store.commit("warningIdListCommit", this.arrIndexList);
          status = true;
        }
        this.$router.push({
          path: "/TwoHandle",
          query: {
            id: e,
            status: status
          }
        });
      } else {
        Toast("您只可以查看");
      }
    },
    clearData() {
      var array = this.listWarningContent;
      for (let index = 0; index < array.length; index++) {
        array[index].color = "background:white";
        array[index].img = "static/imgs/checkedNo.png";
      }
      this.listWarningContent = array;
      if (this.showChecked === false) {
        this.arrIndexList = [];
        this.$store.commit("warningIdListCommit", this.arrIndexList);
        this.timePermision = 0;
        this.startTimeSelect = 0;
      }
    },
    warningDataSelect(index, event) {
      if (this.listWarningContent[index].showIndex === true) {
        this.listWarningContent[index].showIndex = false;
        this.listWarningContent[index].img = "static/imgs/checkedNo.png";
        this.arrIndexList.splice(index, 1);
        if (this.arrIndexList.length === 0) {
          this.clearData();
          this.showCheckedFunc();
        }
      } else {
        this.listWarningContent[index].showIndex = true;
        this.listWarningContent[index].img = "static/imgs/checked.png";
        this.arrIndexList.push({ index: index, warningId: event.warningId });
      }
    },
    refresh() {
      this.refreshing = true;
      this.$refs.container.scrollTop = 0;

      this.refreshing = false;
      this.pageSizeCont = heightDevice;
      this.initData();
    },
    load() {
      this.loading = true;

      this.loading = false;
      this.pageSizeCont += 10;
      this.initData();
    },
    selectDataclassType() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classType, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.warningTypeArr, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择预警类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          // console.log(selectArr[0]);
          _this.warningType = selectArr[0];

          // _this.$store.state.warningStation = selectArr[0];
          _this.open = true;
        }
      });
    },
    selectDataPicoptions() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          // console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.$store.state.warningStation = selectArr[0];
          _this.open = true;
        }
      });
    },

    close() {
      this.openTime = false;
      this.value1 = null;
      this.value2 = null;
      this.normal.value1 = null;
      this.initData();
    },
    confirmTime(value) {
      this.openTime = false;
      switch (this.Time) {
        case "start":
          this.value1 = AppBace.filter(value);
          this.$store.commit("startTimeCommit", this.value1);

          break;
        case "end":
          this.value2 = AppBace.filter(value);
          this.$store.commit("lastTimeCommit", this.value2);
          break;
      }
    },
    closeBottomSheetTime() {
      this.openTime = false;
    },
    selectStartTime(e) {
      this.openTime = true;
      this.Time = e;
    },
    selectLastTime(e) {
      this.Time = e;
      this.openTime = true;
    },
    handled() {
      // var arr = this.listWarningContent;
      this.showHandled = !this.showHandled;
      if (this.showHandled) {
        var arr = this.$store.state.handle;
        this.styleHandle = "color:#ccc;border:none";
        if (!this.showNoHandled) {
          this.listWarningContent = WarningHandled.Processed(arr, 0);
        } else {
          this.listWarningContent = [];
        }
      } else {
        var arr = this.$store.state.handle;
        this.styleHandle = "";
        if (!this.showNoHandled) {
          this.listWarningContent = arr;
        } else {
          this.listWarningContent = WarningHandled.Processed(arr, 1);
        }
      }
    },
    nohandled() {
      var arr = this.$store.state.handle;
      this.showNoHandled = !this.showNoHandled;
      if (this.showNoHandled) {
        this.styleNoHandle = "color:#ccc;border:none";
        if (!this.showHandled) {
          this.listWarningContent = WarningHandled.Processed(arr, 1);
        } else {
          this.listWarningContent = [];
        }
      } else {
        this.styleNoHandle = "";
        if (!this.showHandled) {
          this.listWarningContent = arr;
        } else {
          this.listWarningContent = WarningHandled.Processed(arr, 0);
        }
      }
    },
    closeBottomSheet() {
      this.open = false;
      this.value1 = null;
      this.value2 = null;
      this.normal.value1 = null;
      this.initData();
    },
    openBotttomSheet() {
      this.open = true;
    },
    backTonoHandleNo(e) {
      // console.log(1);
      var status = false;
      if (this.user !== "manager") {
        // this.$store.commit("warningIdListCommit", this.arrIndexList);
        this.$router.push({
          path: "/TwoHandle",
          query: {
            id: e,
            status: status
          }
        });
      } else {
        Toast("您只可以查看");
      }
    },
    handledTo(e) {
      var status = false;
      if (this.user !== "manager") {
        if (e === "批量处理" && this.arrIndexList.length > 0) {
          if (this.arrIndexList.length > 1) {
            this.$store.commit("warningIdListCommit", this.arrIndexList);
            status = true;
            this.$router.push({
              path: "/TwoHandle",
              query: {
                id: e,
                status: status
              }
            });
          }
        } else {
          Toast("请选择您要处理的内容！");
        }
      } else {
        Toast("您只可以查看");
      }
    },
    // openSimpleDialog(e) {
    //   this.openSimple = true;
    //   this.content = Warning.warningContent(this.listWarningContent, e);
    //   console.log(e);
    // },
    // closeSimpleDialog() {
    //   this.openSimple = false;
    // },
    toggle(panel, index) {
      this.panel = panel === this.panel ? "" : panel;
      this.content = Warning.warningContent(this.listWarningContent, index);
      // debugger
    },
    backTonoHandle(e, index) {
      this.$router.push({
        path: "/Handle",
        query: {
          id: e,
          startTime: this.value1,
          lastTime: this.value2,
          waterStation: this.normal.value1,
          pageSize: this.pageSizeCont,
          pageNum: this.currentPage,
          warningType: this.warningType,
          index: index
        }
      });
    },
    toBack() {
      // this.$router.go(-1);
      if (this.user === "manager") {
        this.$router.go(-1);
        this.$store.state.lastTime = "";
        this.$store.state.startTime = "";
      } else {
        this.$router.go(-1);
        this.$store.state.lastTime = "";
        this.$store.state.startTime = "";
      }
    },
    async initData() {
      this.open = false;
      var _this = this;
      var url = "/warning/list";
      if (this.$store.state.warningStation !== "") {
        var waterStation = this.$store.state.warningStation;
      } else {
        var waterStation = this.normal.value1;
      }
      // var waterStation = "沙井二期入水口水质监测站";
      // console.log(this.$store.state.startTime);
      var account = 0;
      if (this.$store.state.startTime !== "") {
        var starttime = this.$store.state.startTime;
        var lasttime = this.$store.state.lastTime;
        this.value1 = starttime;
        this.value2 = lasttime;
      } else {
        var starttime = this.value1;
        var lasttime = this.value2;
        // debugger
        this.$store.commit("startTimeCommit", this.value1);
        this.$store.commit("lastTimeCommit", this.value2);
        // this.$store.state.startTime = startTime;
        // this.$store.state.lastTime = lastTime;
      }
      this.$store.commit("warningStationHandle", waterStation);
      var pageSize = this.pageSizeCont;
      var pageNum = this.currentPage;
      var warningType = this.warningType;
      var res = await AppBace.request(
        url,
        "POST",
        {
          waterStation,
          starttime,
          lasttime,
          pageSize,
          pageNum,
          warningType
        },
        this.token
      );
      console.log("res", res);
      this.undo = res.data.data.undo;
      this.showover = false;
      var sizePage = res.data.data.total;
      // debugger
      this.sizeContentAll = sizePage;
      this.sizeContent = Math.floor(sizePage / this.pageSizeCont);
      // debugger
      this.$store.commit("warningContent", res.data.data.data);
      // this.currentPage = this.currentPage + 10;
      var array = [];
      array = res.data.data.data;
      this.total = res.data.data.total;
      var arr = [];
      var newArr = [];
      for (let index = 0; index < array.length; index++) {
        // arr.push(array[index]);

        var arrAddNew = {
          showIndex: false,
          img: "static/imgs/checkedNo.png",
          color: "#CCC"
        };
        arr = {
          ...array[index],
          ...arrAddNew
        };
        newArr.push(arr);
      }
      // console.log(arr);
      this.listWarningContent = newArr;
      // debugger;
      this.list = this.listWarningContent;
      // console.log(this.list);
    }
  },
  mounted() {
    this.user = sessionStorage.getItem("user");
    this.token = sessionStorage.getItem("token");
    this.options = this.$store.state.menu;
    if (
      this.$route.query.stationName !== "" &&
      this.$route.query.stationName !== undefined &&
      this.$route.query.stationName !== null
    ) {
      this.$store.state.warningStation = "";
      this.normal.value1 = this.$route.query.stationName;
    } else {
      this.normal.value1 = this.$store.state.menu[0];
    }
    this.initData();
  }
  // components: {
  //   MescrollVue
  // }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.bettweenFlex {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  height: 30px;
}
.bettweenFlexBack {
